  function delay(ms) {
    return new Promise(function(resolve, reject) {
        setTimeout(() => resolve(), ms);
    });
}

function getHTML(url) {
    return new Promise(resolve => {
        delay(300).then(function() {
            axios.get(url).then(function(data) {
                resolve(data);
            });
        });
    })
}
